@import '../../variables.scss';

label {
    display: block;
    font-size: 14px;
    margin: 0;
    margin-bottom: 10px;
    color: $charcoal;
}

input[type=text], textarea {
    background-color: transparent;
    width: 100%;
    border:none;
    color: $primary-darker;
    border: 1px solid transparent;
    border-bottom-color: $text-dark;
    font-size: 14px;
    padding: 10px;
    box-sizing: border-box;
    font-weight: $SemiBold;
    transition: 0.1s;
    &.left { text-align: left }
    &.right { text-align: right }
    &:focus {
        // outline: none;
        // border: 1px solid $charcoal;
        // border-radius: 7px;
        // box-shadow: none;
        outline: none;
        border-bottom-color: $white;
        box-shadow: 0 2px 5px #d0d0d0;
        border-radius: 10px;
    }
    &.title {
        font-size: 17px;
        max-width: 200px;
        color: $charcoal;
        border: 2px solid transparent;
        box-sizing: border-box;
        &:focus {
            border: 3px solid $charcoal;
            border-radius: 10px;
            box-shadow: none;
        }
    }
}

input[type=radio] {
    
}

input {
    height: 35px;
}

textarea {
    resize: none;
}

.danger {
    input, textarea {
        border-bottom-color: $danger !important;
        &:focus {
            border-color: $danger !important;
        }
    }
}

.error-message {
    font-size: 12px;
    font-weight: bold;
    color: $danger;
    padding: 5px;
    display: block;
    margin-top: 5px;
}