@import '../../variables.scss';

.select-container {
    padding: 5px 10px;
    height: 100%;
    max-width: 160px;
    max-height: 25px;
    background-color: $white;
    box-sizing: border-box;
    box-shadow: 0 1px 2px #00000030 !important;
    border-radius: 14px;
    overflow: hidden;
    transition: 0.2s;
    display: inline-block;
    position: relative;
    &.active {
        max-height: 200px;
        z-index: 88;
    }
    .options {
        border-top: 1px solid #dcdcdc;
        margin-top: 5px;
        padding-top: 5px;
        max-height: 161px;
        overflow: auto;
        li {
            button {
                padding: 5px 7px;
                font-size: 13px;
                color: $charcoal;
                font-weight: $SemiBold;
                transition: 0.1s;
                border-radius: 8px;
                background: none;
                border: none;
                width: calc(100% - 5px);
                text-align: left;
                &.active, &:hover {
                    &:not(.disabled) {
                        color: $white;
                        background-color: $charcoal;
                        box-shadow: 0 1px 2px #00000040;
                    }
                }
                &.disabled {
                    color: #d0d0d0;
                    cursor: not-allowed;
                }
            }
        }
        &::-webkit-scrollbar-track {
            background: $white !important;
        }
    }
    cursor: pointer;
    input {
        font-size: 12px;
        padding: 0;
        height: 16px;
        border: none;
        cursor: pointer;
        outline: none;
        &:focus {
            box-shadow: none;
            border: none;
        }
    }
}